import { Button, Input, Spin } from 'antd'
import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import { getClientDataAction, getHousesAction } from '../../../redux/dataDuck'
import text from '../../text'

const AdminChoose = ({houses, language, fetching, selectedUserData, error, getData, getHouses}) => {

    useEffect(() => {
        getData()
    }, [])

    return (
        <div className='application-container'>
             {
                fetching ? 
                    <div className='spinner-full-page'><Spin size='large'/></div>
                    :
                    <div className="application-content">
                        <div>
                            <div className="app-top-icon">
                                <img src="/img/sidebar/buy.svg" alt="profile"/>
                                <p>{text.sidebar.userBoughtVilla[language]}</p>
                            </div>
                            <h2>{selectedUserData.name} {selectedUserData.lastName}</h2>
                        </div>
                        <div className="admin-house-choose">
                            <p>{text.admin.purchasePriceApproved[language]}:</p>
                            <p className='admin-response'>{selectedUserData.priceApproved ? text.responses.yes[language] : text.responses.no[language]}</p>
                            <p>{text.admin.blueprintsApproved[language]}:</p>
                            <p className='admin-response'>{selectedUserData.villaApproved ? text.responses.yes[language] : text.responses.no[language]}</p>
                        </div>
                        <div className="multiple-buttons">
                            <Link to='/admin/financing'><Button loading={fetching} className='nav-button'>{text.button.returnCaps[language]}</Button></Link>
                            <Link to='/admin/interior-design'><Button className='nav-button'>{text.button.continue[language]}</Button></Link>
                        </div>
                    </div>
            }
        </div>
    )
}

let mapState = ({
    app: {language},
    data: {fetching, selectedUserData, error, houses}
}) => ({
    language,
    fetching,
    selectedUserData,
    error,
    houses
})

export default connect(
    mapState,
    {
        getData: getClientDataAction,
        getHouses: getHousesAction
    }
)(AdminChoose)
