import React, { useEffect, useState } from 'react'
import { Button, Input, message, Modal, Select, Spin } from 'antd'
import { connect } from 'react-redux'
import { getDataAction, handleInteriorDesignAction, sendNotificationAction, updateUserAction } from '../../redux/dataDuck'
import text from '../text'
import { Link } from 'react-router-dom'
import ReactImageMagnify from 'react-image-magnify'
import ConfirmationModal from '../common/ConfirmationModal'
import { handleConfirmationBoxAction } from '../../redux/appDuck'
import { Document, Page, PDFViewer, StyleSheet, Text, View } from '@react-pdf/renderer'
let { Option } = Select

const InteriorDesign = ({sendNotification, info, dataUpdated, history, fetching, language, getData, updateUser, handleInfo, toggleModal}) => {

    let [background, setBackground] = useState('/img/intDesign/countertops/GraphiteCemento.jpg')
    let [docModal, setDocModal] = useState(false)

    let handleSubmit = () => {
        
        toggleModal()
    }

    let confirmAction = async () => {
        sendNotification('intDesign')
        await updateUser()
        toggleModal()
        setDocModal(true)
    }

    let handleDoc = () => {
        if( 
            !info.kitchenCountertop ||
            !info.bathroomCountertop
            ) return message.error(text.interiorDesign.missingSelection[language])
            if((!info.kitchenTopCabinet || !info.kitchenBottomCabinet) && !info.bathroomCabinet) return message.error(text.interiorDesign.missingSelection[language])
            if(!info.kitchenTopCabinet && !info.kitchenBottomCabinet) return message.error(text.interiorDesign.missingSelection[language])
        setDocModal(true)
    }

    let handleBackground = (option, value) => {
        switch(value){
            case 'Aniz':
                handleInfo(option, value)
                return setBackground('/img/intDesign/cabinets/aniz.jpg')
            case 'Avellana':
                handleInfo(option, value)
                return setBackground('/img/intDesign/cabinets/avellana.jpg')
            case 'Nogal':
                handleInfo(option, value)
                return setBackground('/img/intDesign/cabinets/nogal.jpg')
            case 'Paja':
                handleInfo(option, value)
                return setBackground('/img/intDesign/cabinets/paja.jpg')
            case 'Rattan':
                handleInfo(option, value)
                return setBackground('/img/intDesign/cabinets/rattan.jpg')
            case 'Graphite Cemento':
                handleInfo(option, value)
                return setBackground('/img/intDesign/countertops/GraphiteCemento.jpg')
            case 'Limestone':
                handleInfo(option, value)
                return setBackground('/img/intDesign/countertops/Limestone.jpg')
            case 'Ornamental':
                handleInfo(option, value)
                return setBackground('/img/intDesign/countertops/Ornamental.jpg')
            case 'Santta Cecilia':
                handleInfo(option, value)
                return setBackground('/img/intDesign/countertops/SantaCecilia.jpg')
            case 'Sand White':
                handleInfo(option, value)
                return setBackground('/img/intDesign/countertops/SandWhite.jpg')
            case 'Snow White':
                handleInfo(option, value)
                return setBackground('/img/intDesign/countertops/SnowWhite.jpg')
            case '':
                handleInfo(option, value)
                return 
            // case 'Aspen Beige':
            //     handleInfo(option, value)
            //     return setBackground('/img/intDesign/demo/floorDemoA.webp')
            // case 'Nevada Gris':
            //     handleInfo(option, value)
            //     return setBackground('/img/intDesign/demo/floorDemoB.webp')
            // case 'Emblem Grey':
            //     handleInfo(option, value)
            //     return setBackground('/img/intDesign/demo/floorDemoC.webp')
            // case 'Emblem Brown':
            //     handleInfo(option, value)
            //     return setBackground('/img/intDesign/demo/floorDemoD.webp')
            // case 'Khalos':
            //     handleInfo(option, value)
            //     return setBackground('/img/intDesign/demo/tileExtraDemo.webp')
            // case 'sameAsFloor':
            //     if(info.floor) {
            //         handleInfo(option, info.floor)
            //         return handleBackground(info.floor)
            //     }
            //     return message.error(text.interiorDesign.missingFloorSelect[language])
            default: 
                return
        }
    }

    useEffect(() => {
        getData()
    }, [])

    const styles = StyleSheet.create({
        page: {
          flexDirection: 'column',
          backgroundColor: 'white',
          justifyContent: 'flex-start',
          padding: 16,
          width: '100%'
        },
        section: {
          marginTop: 10,
          padding: '0 10px'
        },
        title: {
            fontSize: 17,
            fontWeight: 'bold',
            marginBottom: 0
        },  
        text: {
            fontSize: 11,
            margin: '5px 0',
            width: '100%',
            fontWeight: 800
        },
        subtitle: {
            marginTop: 10,
            fontSize: 10,
            margin: '5px 0',
            textDecoration: 'underline'
        },
        firstText: {
            fontSize: 10,
            fontWeight: 300
        }
      })

    return (
        <div className='application-container'>
             {
                fetching ? 
                    <div className='spinner-full-page'><Spin size='large'/></div>
                    :
                    <div className="application-content no-justify">
                        <div>
                            <div className="app-top-icon">
                                <img src="/img/sidebar/intDesign.svg" alt="profile"/>
                                <p>{text.sidebar.interiorDesign[language]}</p>
                            </div>
                            <h2>{text.sidebarSubtitle.interiorDesign[language]}</h2>
                        </div>
                        
                        <div className='padded-app-container form-container'>
                            <div className="int-design-container">
                                <div className="int-design-left">
                                    <ReactImageMagnify
                                    {...{
                                        smallImage: {
                                            alt: 'Demo',
                                            isFluidWidth: true,
                                            src: background
                                        },
                                        largeImage: {
                                            src: background,
                                            width: 1200,
                                            height: 1800
                                        },
                                        enlargedImageContainerDimensions: {
                                            width: 300,
                                            height: '100%'
                                        }
                                    }} 
                                    />
                                </div>
                                <div className="int-design-right">
                                    <div className='int-design-select'>
                                        <p>{text.interiorDesign.kitchenCabinets[language]}</p>
                                        <small>{text.interiorDesign.topCabinet[language]}</small>
                                        <div className="sample-container">
                                            <div className={info.kitchenTopCabinet === 'Aniz' ? 'sample-item active-sample' : 'sample-item'} onClick={() => {handleBackground('kitchenTopCabinet', 'Aniz'); handleBackground('bathroomCabinet', '')}} style={{backgroundImage: "url(/img/intDesign/cabinets/aniz.jpg"}}></div>
                                            <div className={info.kitchenTopCabinet === 'Avellana' ? 'sample-item active-sample' : 'sample-item'} onClick={() => {handleBackground('kitchenTopCabinet', 'Avellana'); handleBackground('bathroomCabinet', '')}} style={{backgroundImage: "url(/img/intDesign/cabinets/avellana.jpg"}}></div>
                                            <div className={info.kitchenTopCabinet === 'Nogal' ? 'sample-item active-sample' : 'sample-item'} onClick={() => {handleBackground('kitchenTopCabinet', 'Nogal'); handleBackground('bathroomCabinet', '')}} style={{backgroundImage: "url(/img/intDesign/cabinets/nogal.jpg"}}></div>
                                            <div className={info.kitchenTopCabinet === 'Paja' ? 'sample-item active-sample' : 'sample-item'} onClick={() => {handleBackground('kitchenTopCabinet', 'Paja'); handleBackground('bathroomCabinet', '')}} style={{backgroundImage: "url(/img/intDesign/cabinets/paja.jpg"}}></div>
                                        </div>
                                        <small>{text.interiorDesign.bottomCabinet[language]}</small>
                                        <div className="sample-container">
                                            <div className={info.kitchenBottomCabinet === 'Aniz' ? 'sample-item active-sample' : 'sample-item'} onClick={() => {handleBackground('kitchenBottomCabinet', 'Aniz'); handleBackground('bathroomCabinet', '')}} style={{backgroundImage: "url(/img/intDesign/cabinets/aniz.jpg"}}></div>
                                            <div className={info.kitchenBottomCabinet === 'Avellana' ? 'sample-item active-sample' : 'sample-item'} onClick={() => {handleBackground('kitchenBottomCabinet', 'Avellana'); handleBackground('bathroomCabinet', '')}} style={{backgroundImage: "url(/img/intDesign/cabinets/avellana.jpg"}}></div>
                                            <div className={info.kitchenBottomCabinet === 'Nogal' ? 'sample-item active-sample' : 'sample-item'} onClick={() => {handleBackground('kitchenBottomCabinet', 'Nogal'); handleBackground('bathroomCabinet', '')}} style={{backgroundImage: "url(/img/intDesign/cabinets/nogal.jpg"}}></div>
                                            <div className={info.kitchenBottomCabinet === 'Paja' ? 'sample-item active-sample' : 'sample-item'} onClick={() => {handleBackground('kitchenBottomCabinet', 'Paja'); handleBackground('bathroomCabinet', '')}} style={{backgroundImage: "url(/img/intDesign/cabinets/paja.jpg"}}></div>
                                        </div>
                                    </div>
                                    <div className='int-design-select'>
                                        <p>{text.interiorDesign.bathroomCabinets[language]}</p>
                                        <div className="sample-container">
                                            {
                                                !info.kitchenTopCabinet || !info.kitchenBottomCabinet ? 
                                                    <>
                                                        <div className={info.bathroomCabinet === 'Aniz' ? 'sample-item active-sample' : 'sample-item'} onClick={() => handleBackground('bathroomCabinet', 'Aniz')} style={{backgroundImage: "url(/img/intDesign/cabinets/aniz.jpg"}}></div>
                                                        <div className={info.bathroomCabinet === 'Avellana' ? 'sample-item active-sample' : 'sample-item'} onClick={() => handleBackground('bathroomCabinet', 'Avellana')} style={{backgroundImage: "url(/img/intDesign/cabinets/avellana.jpg"}}></div>
                                                        <div className={info.bathroomCabinet === 'Nogal' ? 'sample-item active-sample' : 'sample-item'} onClick={() => handleBackground('bathroomCabinet', 'Nogal')} style={{backgroundImage: "url(/img/intDesign/cabinets/nogal.jpg"}}></div>
                                                        <div className={info.bathroomCabinet === 'Paja' ? 'sample-item active-sample' : 'sample-item'} onClick={() => handleBackground('bathroomCabinet', 'Paja')} style={{backgroundImage: "url(/img/intDesign/cabinets/paja.jpg"}}></div>
                                                    </>
                                                :
                                                    <>
                                                        <div className='sample-item active-sample' style={{backgroundImage: `url(/img/intDesign/cabinets/${info.kitchenTopCabinet.toLowerCase()}.jpg`}}></div>
                                                        <div className='sample-item active-sample' style={{backgroundImage: `url(/img/intDesign/cabinets/${info.kitchenBottomCabinet.toLowerCase()}.jpg`}}></div>
                                                    </>
                                            }
                                        </div>
                                    </div>
                                    <div className='int-design-select'>
                                        <p>{text.interiorDesign.kitchenCountertop[language]}</p>
                                        <div className="sample-container">
                                            <div className={info.kitchenCountertop === 'Graphite Cemento' ? 'sample-item active-sample' : 'sample-item'} onClick={() => {handleBackground('kitchenCountertop', 'Graphite Cemento'); handleBackground('bathroomCountertop', 'Graphite Cemento')} } style={{backgroundImage: "url(/img/intDesign/countertops/GraphiteCemento.jpg"}}></div>
                                            <div className={info.kitchenCountertop === 'Limestone' ? 'sample-item active-sample' : 'sample-item'} onClick={() => {handleBackground('kitchenCountertop', 'Limestone'); handleBackground('bathroomCountertop', 'Limestone')}} style={{backgroundImage: "url(/img/intDesign/countertops/Limestone.jpg"}}></div>
                                            <div className={info.kitchenCountertop === 'Ornamental' ? 'sample-item active-sample' : 'sample-item'} onClick={() => {handleBackground('kitchenCountertop', 'Ornamental'); handleBackground('bathroomCountertop', 'Ornamental')}} style={{backgroundImage: "url(/img/intDesign/countertops/Ornamental.jpg"}}></div>
                                            <div className={info.kitchenCountertop === 'Santa Cecilia' ? 'sample-item active-sample' : 'sample-item'} onClick={() => {handleBackground('kitchenCountertop', 'Santta Cecilia'); handleBackground('bathroomCountertop', 'Santta Cecilia')}} style={{backgroundImage: "url(/img/intDesign/countertops/SantaCecilia.jpg"}}></div>
                                            <div className={info.kitchenCountertop === 'Sand White' ? 'sample-item active-sample' : 'sample-item'} onClick={() => {handleBackground('kitchenCountertop', 'Sand White'); handleBackground('bathroomCountertop', 'Sand White')}} style={{backgroundImage: "url(/img/intDesign/countertops/SandWhite.jpg"}}></div>
                                            <div className={info.kitchenCountertop === 'Snow White' ? 'sample-item active-sample' : 'sample-item'} onClick={() => {handleBackground('kitchenCountertop', 'Snow White'); handleBackground('bathroomCountertop', 'Snow White')}} style={{backgroundImage: "url(/img/intDesign/countertops/SnowWhite.jpg"}}></div>
                                        </div>
                                    </div>
                                    <div className='int-design-select'>
                                        <p>{text.interiorDesign.bathroomCountertop[language]}</p>
                                        <div className="sample-container">
                                            {
                                                info.bathroomCountertop ? 
                                                    <div className='sample-item active-sample' style={{backgroundImage: `url(/img/intDesign/countertops/${info.kitchenCountertop}.jpg`}}></div>
                                                :  
                                                    <small>Select a kitchen countertop</small>
                                            }
                                        </div>
                                    </div>
                                    {/* <div className='int-design-select'>
                                        <p>{text.interiorDesign.flooring[language]}</p>
                                        <div className="sample-container">
                                            
                                        </div>
                                    </div>
                                    <div className='int-design-select'>
                                        <p>{text.interiorDesign.showerTile[language]}</p>
                                        <div className="sample-container">
                                            
                                        </div>
                                    </div> */}
                                </div>
                            </div>
                        </div>
                        <div className='multiple-buttons intd-buttons small-buttons'>
                            <Link to='/buy'><Button loading={false} className='nav-button'>{text.button.returnCaps[language]}</Button></Link>
                            <Button onClick={handleSubmit} loading={false} className='nav-button'>{text.button.continue[language]}</Button>
                            <Button onClick={handleDoc} loading={false} className='nav-button'>{text.button.print[language]}</Button>
                        </div>
                    </div>
            }
            <ConfirmationModal confirmAction={confirmAction}>
                <div className="modal-container-confirm">
                    <p>{text.interiorDesign.kitchenTop[language]}: <span>{info.kitchenTopCabinet ? info.kitchenTopCabinet : info.kitchenBottomCabinet}</span></p>
                    <p>{text.interiorDesign.kitchenBottom[language]}: <span>{info.kitchenBottomCabinet ? info.kitchenBottomCabinet : info.kitchenTopCabinet}</span></p>
                    <p>{text.interiorDesign.bathCab[language]}: <span>{info.bathroomCabinet ? info.bathroomCabinet : `${info.kitchenTopCabinet}, ${info.kitchenBottomCabinet}`}</span></p>
                    <p>{text.interiorDesign.kitchenCounter[language]}: <span>{info.kitchenCountertop}</span></p>
                    <p>{text.interiorDesign.bathCounter[language]}: <span>{info.bathroomCountertop}</span></p>
                    {/* <p>{text.interiorDesign.floors[language]}: <span>{info.floor}</span></p>
                    <p>{text.interiorDesign.showerTile[language]}: <span>{info.showerTile}</span></p> */}
                    <h3>{text.interiorDesign.warning[language]}</h3>
                </div>
            </ConfirmationModal>
            <div >
                <Modal
                    title={''}
                    visible={docModal}
                    onCancel={() => setDocModal(false)}
                    okText={'OK'}
                    onOk={() => setDocModal(false)}
                    className='doc-modal'
                >
                    <div className="modal-container-doc">
                    <PDFViewer>
                        <Document>
                        <Page size="A4" style={styles.page}>
                            <View style={styles.section}>
                                <Text style={styles.title}>PUNTA PIEDRA, {text.interiorDesign.docFinishes[language]}</Text>
                            </View>
                            <View style={styles.section}>
                                <Text style={styles.text}><Text style={styles.firstText}>{text.interiorDesign.kitchenTop[language]}: </Text>{info.kitchenTopCabinet ? info.kitchenTopCabinet : info.kitchenBottomCabinet}</Text>
                                <Text style={styles.text}><Text style={styles.firstText}>{text.interiorDesign.kitchenBottom[language]}: </Text>{info.kitchenBottomCabinet ? info.kitchenBottomCabinet : info.kitchenTopCabinet}</Text>
                                <Text style={styles.text}><Text style={styles.firstText}>{text.interiorDesign.bathCab[language]}: </Text>{info.bathroomCabinet ? info.bathroomCabinet : `${info.kitchenTopCabinet}, ${info.kitchenBottomCabinet}`}</Text>
                                <Text style={styles.text}><Text style={styles.firstText}>{text.interiorDesign.kitchenCounter[language]}: </Text>{info.kitchenCountertop}</Text>
                                <Text style={styles.text}><Text style={styles.firstText}>{text.interiorDesign.bathCounter[language]}: </Text>{info.bathroomCountertop}</Text>
                            </View>
                        </Page>
                        </Document>
                    </PDFViewer>
                    </div>
                </Modal>
            </div>
        </div>
    )
}

let mapState = ({
    app: {language},
    user: {fetching},
    data: {info, dataUpdated}
}) => ({
    language,
    fetching,
    info,
    dataUpdated
})

export default connect(
    mapState,
    {
        getData: getDataAction,
        updateUser: updateUserAction,
        handleInfo: handleInteriorDesignAction,
        toggleModal: handleConfirmationBoxAction,
        sendNotification: sendNotificationAction
    }
)(InteriorDesign)
